import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
// import OnlineRezultati from '../views/OnlineRezultati.vue'
// import Cenovnik from '../views/Cenovnik.vue'


Vue.use(VueRouter)

const routes = [{
        path: '/home',
        name: 'Home', //najcesca pitanja
        component: Home
    },
    {
        path: '/about',
        name: 'About',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () =>
            import ('../views/About.vue')
    },
    {
        path: '/onlineRezultati',
        name: 'OnlineRezultati',
        component: () =>
            import ('../views/OnlineRezultati.vue')
    },
    {
        path: '/cenovnik',
        name: 'Cenovnik',
        component: () =>
            import ('../views/Cenovnik.vue')
    },
    {
        path: '/najcescaPitanja',
        name: 'NajcescaPitanja',
        component: () =>
            import ('../views/NajcescaPitanja.vue')
    },


    // {
    //     path: '/404',
    //     name: '404',
    //     component: () =>
    //         import ('./views/PageNotFound.vue')
    // }
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router