import Vue from 'vue'
import Vuex from 'vuex'
import * as firebase from 'firebase'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        Intro: true,
        user: {
            id: '',
        },
        OnlineRezultati: false,
        // snackbar: true
    },
    mutations: {
        setUser(state, payload) {
            state.user = payload
        },
        ONLINE_REZULTATI(state, payload) {
            state.OnlineRezultati = payload
        }
        // toggleSnackbar(state) {
        //     state.snackbar = !state.snackbar
        // }
    },
    // getters: {
    //     showSnackbar: state => {
    //         return state.snackbar
    //     }
    // },
    actions: {
        SignUserUp(setUser, payload) {
            firebase.auth().createUserWithEmailAndPassword(payload.email, payload.password)
                .then(
                    // this.state.snackbar = true,
                    // console.log(this.state.snackbar)
                )
                .catch(function(error) {
                    // Handle Errors here.
                    var errorCode = error.code;
                    var errorMessage = error.message;
                    console.log(errorCode)
                    console.log(errorMessage)
                        // if (errorCode == 'auth/weak-password') {
                        //     alert('The password is too weak.');
                        // } else {
                        // this.$emit('UspesnaRegistracija')

                    // commit('toggleSnackbar')

                    // }
                    // ...
                });
        },
        PromeniStanjeOnlineRezultati(context, payload) {
            context.commit("ONLINE_REZULTATI", payload);
        }
    },
    modules: {}
})